.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.nav_bar{
  display: flex inline;
}
.main_todo_container{
  text-align: center;
  margin: 20px;
  min-height: 80vh; /*giving this due to this our footer is very stable at bottom no isseues at*/
}
.main_list{
  /* below is the very imp code when we want all element get adjusted on screen presilty even when we change size of screen*/
  display: flex;
  flex-wrap: wrap;


}
.singleTodo_list{
  display: flex;
  flex-direction: column;
  width: 20%;
  height: 300px;
  margin: 20px;
  background: #61dafb;
  border-radius: 15px;
  border: none;
  border-width: 0px;
  padding: 10px;
  overflow-wrap: break-word;
  justify-content: space-between;



}

#para_desc{
  overflow: scroll;
  overflow-x: hidden;
  overflow-y: auto;
}

