/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey; 
  border-radius: 10px;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: rgb(78, 78, 252); 
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: blue; 
}



.singleTodo_list #btn{
  
  border-radius: 15px;
  margin: 5px;
  border: none;
  background-color: blue;
  color: white;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: large;
  width: 45%;
  font-weight: bold;
}

.singleTodo_list #btn:hover{
  background-color: rgb(85, 85, 255);
}


#no_todo_head{
  align-items: center;
  justify-content: center;
  margin: auto;
}
#new_todo_btn{
  font-weight: bold;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: x-large;
  height: 100%;
  border-radius: 0px;
  border: none;
  background-color: #61dafb;
}







/*to make website resposive we have added the break pointes so if device width is less than 800px we are changig some designs aspect*/

/* Use a media query to add a breakpoint at 800px: */

@media screen and (max-width: 1139px) {
  .singleTodo_list {
    width: 25%;
}
}


@media screen and (max-width: 924px) {
  .singleTodo_list {
    width: 27%;
}
}

@media screen and (max-width: 854px) {
  .singleTodo_list {
    width: 40%;
}
}

@media screen and (max-width: 590px) {
  .singleTodo_list {
    width: 75%;
}
}
/*to make website resposive we have added the break pointes so if device width is less than 500px we are changig some designs aspect*/

/* Use a media query to add a breakpoint at 500px: */


/*to make website resposive we have added the break pointes so if device width is less than 420px we are changig some designs aspect*/

/* Use a media query to add a breakpoint at 420px: */

@media screen and (max-width: 280px) {
  .main_list{
    display: flex inline-block;
  }
  .singleTodo_list {
      width: 90%;
  }
  .singleTodo_list #btn{
    width: 40%;
  }
}

@media screen and (max-width: 420px) {
  .main_list{
    display: flex inline-block;
  }
  .singleTodo_list {
      width: 90%;
  }
}